







import Vue from "vue";
import Header from "@/components/UI/Header.vue";
import { mapGetters } from "vuex";
export default Vue.extend({
  components: {
    Header
  },
  computed: {
    ...mapGetters("user", ["getUser"]),
    user: {
      get(): object {
        return this.getUser;
      }
    }
  }
});
