































import Vue from "vue";
import GalleryItem from "@/components/GalleryItem.vue";
import { mapGetters } from "vuex";
import { Post } from "@/types";

export default Vue.extend({
  components: { GalleryItem },
  props: {
    posts: {
      type: Array
    }
  },
  data() {
    return {
      searchArray: null,
      search: "",
      totalSize: 4,
      isEmpty: true
    };
  },
  computed: {
    ...mapGetters("posts", ["getPosts"]),
    articles: {
      get(): Array<object> {
        const articleList = this.getPosts;
        return articleList.slice(0, this.totalSize);
      }
    }
  },

  methods: {
    loadMore() {
      this.totalSize *= 2;
    },
    clear() {
      this.search = "";
      this.findAuthor();
    },
    searchByAuthor(value: string) {
      this.search = value;
      this.findAuthor();
    },
    findAuthor(): void {
      if (this.search) {
        this.searchArray = this.getPosts.filter((item: Post) => {
          return item.author.toLowerCase() === this.search.toLowerCase();
        });
        this.isEmpty = false;
      } else {
        this.searchArray = null;
        this.isEmpty = true;
      }
    }
  }
});
